import React, { Component } from 'react';
import './app.css';
import Button from '../button/button';

export default class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isClickable:true,
			formula: '',
			output: '0',
			currentValue:'0'
		}
		let lastChar = this.state.formula.slice(-1);
	}
	isError = () => {
		const wrongValue = ['wrong number']
		if (wrongValue.includes(this.state.formula.toLowerCase())) {
			return true
		}
		else {
			return false
		}
	}
	addNumbers = (value) => {
		if (this.isError()) {
			this.setState({
				formula: value,
				output: '0',
				currentValue:'0'
			})
		}
		else if(this.state.output.length>15){
			this.setState({
				output:'too much numbers',
				isClickable:false,
			})
			setTimeout(()=>{
				this.setState({
					output:this.state.currentValue,
					isClickable:true
				})
			},500)
		}
		else if (this.state.output === '0') {
			this.setState({
				formula: this.state.formula + value,
				output: value,
				currentValue:value
			})
		}
		else {
			this.setState({
				formula: this.state.formula + value,
				output: this.state.output + value,
				currentValue:this.state.currentValue + value
			})
		}
	}
	addZero = () => {
		let lastChar = this.state.formula.slice(-1);
		if (this.isError()) {
			this.setState({
				formula: '0',
				output: '0',
				currentValue:'0'
			})
		}
		else if (this.state.output === '0') {
			if (lastChar === '0') {
				return false
			}
			else {
				this.setState({
					formula: this.state.formula + '0',
					output: '0',
					currentValue:'0'
				})
			}
		}
		else {
			this.setState({
				formula: this.state.formula + '0',
				output: this.state.output + '0',
				currentValue:this.state.currentValue+'0'
			})
		}
	}
	addAction = (value) => {
		let lastChar = this.state.formula.slice(-1);
		if (this.isError()) {
			this.setState({
				formula: value,
				output: '0',
				currentValue:'0'
			})
		}
		else if ((/[+-/*]/).test(lastChar)) {
			let newFormula = this.state.formula.replace(/.$/, value)
			this.setState({
				formula: newFormula,
				output: '0',
				currentValue:'0'
			})
		}
		else {
			this.setState({
				formula: this.state.formula + value,
				output: '0',
				currentValue:'0'
			})
		}
	}

	reset = () => {
		this.setState({
			formula: '',
			output: '0',
			currentValue:'0'
		})
	}

	delete = () => {
		let newFormula = this.state.formula.slice(0, -1);
		let newOutput = this.state.output === '0' ? '0' : this.state.output.slice(0, -1);
		if (this.isError() || this.state.formula.length === 1) {
			this.setState({
				formula: '',
				output: '0',
				currentValue:'0'
			})
		}
		else {
			this.setState({
				formula: newFormula,
				output: newOutput,
				currentValue:newOutput
			})
		}
	}

	equals = () => {
		try {
			let equalsNumber = eval(this.state.formula);
			if(!this.state.formula){
				return false
			}
			else if (String(equalsNumber).toLowerCase() === 'infinity' || String(equalsNumber).toLowerCase() === 'nan') {
				this.setState({
					formula: 'wrong number',
					output: '0',
					currentValue:'0'
				})
			}
			else {
				this.setState({
					formula: String(equalsNumber),
					output: '0',
					currentValue:'0'
				})
			}
		}
		catch{
			this.setState({
				formula: 'wrong number',
				output: '0',
				currentValue:'0'
			})
		}
	}

	addDecimal = (value) => {
		if (this.isError()) {
			this.setState({
				formula: value,
				output: '0',
				currentValue:'0'
			})
		}
		else if (this.state.output.includes(value)) {
			return false
		}
		else {
			this.setState({
				formula: this.state.formula + value,
				output: this.state.output + value,
				currentValue:this.state.currentValue+value
			})
		}
	}
	render() {
		return (
			<div className="app-wrap">
				<div className="calculator">
					<div className="formula-screen">{this.state.formula}</div>
					<div className="output-screen">{this.state.output}</div>
					<div className="panel">
						<Button id="one" value="1" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>1</Button>
						<Button id="two" value="2" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>2</Button>
						<Button id="three" value="3" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>3</Button>
						<Button id="four" value="4" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>4</Button>
						<Button id="five" value="5" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>5</Button>
						<Button id="six" value="6" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>6</Button>
						<Button id="seven" value="7" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>7</Button>
						<Button id="eight" value="8" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>8</Button>
						<Button id="nine" value="9" onToggleClick={this.addNumbers} isClickable={this.state.isClickable}>9</Button>
						<Button id="zero" value="0" onToggleClick={this.addZero} isClickable={this.state.isClickable}>0</Button>
						<Button id="reset" value="AC" onToggleClick={this.reset}
							style={{
								background: '#a71d1d'
							}}
							isClickable={this.state.isClickable}>AC
						</Button>
						<Button id="delete" value="C" onToggleClick={this.delete}
							style={{
								background: '#a71d1d'
							}} 
							isClickable={this.state.isClickable}>C
						</Button>
						<Button id="divide" value="/" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}
							isClickable={this.state.isClickable}>/
						</Button>
						<Button id="multiply" value="*" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}
							isClickable={this.state.isClickable}>x
						</Button>
						<Button id="minus" value="-" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}
							isClickable={this.state.isClickable}>-
						</Button>
						<Button id="plus" value="+" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}
							isClickable={this.state.isClickable}>+
						</Button>
						<Button id="decimal" value="." onToggleClick={this.addDecimal}>.</Button>
						<Button id="equals" value="=" onToggleClick={this.equals}
							style={{
								background: '#140d56'
							}}
							isClickable={this.state.isClickable}>=
						</Button>
					</div>
				</div>
			</div>
		)
	}
}