import React from 'react';

const Button=(props)=>{
	let clazz=props.isClickable?'panel-button':'panel-button disabled'
	return(
		<button className={clazz}
		style={props.style||null}
		onClick={()=>props.onToggleClick(props.value)}
		id={props.id}>
		{props.children}
		</button>
	)
}

export default Button;